.upload-page-container {
    padding-top: 2rem;
    padding-bottom: 2rem;
}

.upload-page-container h2 {
    text-align: center !important;
}
.upload-page-container p {
    text-align: center !important;
    color: var(--text-color-dark);
    margin-top: -0.5rem;
}

.upload-form {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    align-items: center !important; /* Center form elements horizontally */
    max-width: 600px; /* Limit form width for better organization */
    margin: 0 auto !important; /* Center the form within the glass container */}

.form-group {
    display: flex;
    flex-direction: column;
}

.form-group label {
    margin-bottom: 1rem;
    font-weight: 600;
    color: #fff ;
}

.form-group textarea {
    width: 100%;
    min-height: 150px;
    padding: 1rem;
    font-family: 'Inter', sans-serif;
    font-size: 1rem;
    color: var(--text-color);
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    resize: vertical;
    transition: all 0.3s ease;
}

.form-group textarea:focus {
    outline: none;
    border-color: var(--primary-color);
    box-shadow: 0 0 0 3px rgba(0, 170, 255, 0.3);
}

.drop-zone {
    border: 2px dashed var(--glass-border-color);
    border-radius: 10px;
    padding: 2rem;
    text-align: center !important;
    
    transition: all 0.3s ease;
    position: relative;
}

.drop-zone:hover,
.drop-zone.dragging {
    border-color: var(--primary-color);
    background-color: rgba(0, 170, 255, 0.1);
}

.drop-zone-input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    
}

.drop-zone-icon {
    font-size: 3rem;
}

.drop-zone-prompt p {
    margin: 0.5rem 0 0;
    color: var(--text-color-dark);
}
.drop-zone-prompt .file-types {

/* FileDropZone Component Styles */
.file-drop-zone-container {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    width: 100%; /* Full width within the form */
    max-width: 500px; /* Limit width for better visual balance */
    margin: 0 auto !important; /* Center the container horizontally */
    align-items: center !important; /* Center content within the container */
}

    font-size: 0.8rem;
}

.file-list {
    margin-top: 1rem;
    background-color: rgba(0,0,0,0.2);
    padding: 1rem;
    border-radius: 10px;
}

.file-list h4 {
    margin: 0 0 0.5rem 0;
    color: #fff ;
}

.file-list ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.file-list li {
    padding: 0.25rem 0;
    color: var(--text-color);
    font-size: 0.9rem;
}

.cta-button {
    
    padding: 1rem 2.5rem;
    font-size: 1.1rem;
    font-weight: 600;
    color: #fff ;
    background: linear-gradient(90deg, var(--primary-color), var(--secondary-color));
    
    border-radius: 50px;
    
    text-decoration: none;
    transition: all 0.3s ease;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    align-self: center;
    position: relative;
    overflow: hidden;
}

.cta-button .button-text {
    position: relative;
    z-index: 2;
}

.cta-button::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    background-color: rgba(0, 170, 255, 0.5);
    transition: width 0.8s ease-out;
    z-index: 1;
}

.cta-button.analyzing::before {
    width: 100%;
}

.cta-button:disabled {
    opacity: 0.7;
    cursor: not-allowed;
    transform: none;
    box-shadow: none;
}

.cta-button:hover:not(:disabled) {
    transform: translateY(-3px) scale(1.05);
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

.message {
    text-align: center !important;
    margin-top: 1.5rem;
    padding: 1rem;
    border-radius: 10px;
    font-weight: 500;
}

.message.success {
    background-color: rgba(0, 255, 128, 0.1);
    color: #00ff80;
    border: 1px solid rgba(0, 255, 128, 0.2);
}

.message.error {
    background-color: rgba(255, 0, 85, 0.1);
    color: #ff0055;
    border: 1px solid rgba(255, 0, 85, 0.2);
}

.skipped-files-report {
    margin-top: 1.5rem;
    background-color: rgba(255, 165, 0, 0.1);
    color: #ffab40;
    border: 1px solid rgba(255, 165, 0, 0.2);
    padding: 1rem;
    border-radius: 10px;
}

.skipped-files-report h4 {
    margin: 0 0 0.5rem 0;
    color: #ffab40;
}

.skipped-files-report ul {
    list-style-type: none;
    padding-left: 0;
    margin: 0;
}

.skipped-files-report li {
    padding: 0.25rem 0;
}

/* Progress Updates Styles */
.progress-updates {
    margin-top: 1.5rem;
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    padding: 1rem;
    max-height: 300px;
    overflow-y: auto;
}

.progress-updates h4 {
    margin: 0 0 1rem 0;
    color: #fff ;
    font-size: 1.1rem;
}

.progress-list {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.progress-item {
    display: flex;
    justify-content: space-between;
    align-items: center !important;
    padding: 0.75rem;
    border-radius: 50px;
    font-size: 0.9rem;
    transition: all 0.3s ease;
}

.progress-item.progress-info {
    background-color: rgba(0, 170, 255, 0.1);
    border: 1px solid rgba(0, 170, 255, 0.2);
    color: #00aaff;
}

.progress-item.progress-success {
    background-color: rgba(0, 255, 128, 0.1);
    border: 1px solid rgba(0, 255, 128, 0.2);
    color: #00ff80;
}

.progress-item.progress-error {
    background-color: rgba(255, 0, 85, 0.1);
    border: 1px solid rgba(255, 0, 85, 0.2);
    color: #ff0055;
}

.progress-item.progress-warning {
    background-color: rgba(255, 165, 0, 0.1);
    border: 1px solid rgba(255, 165, 0, 0.2);
    color: #ffab40;
}

.progress-item.progress-processing {
    background-color: rgba(138, 43, 226, 0.1);
    border: 1px solid rgba(138, 43, 226, 0.2);
    color: #8a2be2;
    animation: pulse 2s infinite;
}

.progress-message {
    flex: 1;
    margin-right: 1rem;
}

.progress-time {
    font-size: 0.8rem;
    opacity: 0.8;
    white-space: nowrap;
}

@keyframes pulse {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0.7;
    }
    100% {
        opacity: 1;
    }
}
