/* Upload Section Headings */
.upload-section {
    margin-bottom: 2rem;
    padding: 1.5rem;
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: 12px;
    border: 1px solid var(--glass-border-color);
    
    transition: all 0.3s ease;
    position: relative;
}

.clickable-upload-section {
    
    transition: all 0.3s ease;
}

.clickable-upload-section:hover {
    background-color: rgba(0, 0, 0, 0.2);
    transform: translateY(-2px);
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

.clickable-upload-section:active {
    transform: translateY(0);
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.section-heading {
    margin: 0 0 0.5rem 0;
    font-size: 1.4rem;
    font-weight: 700;
    color: #fff;
    text-align: center;
    letter-spacing: 0.5px;
    padding-left: 0;
}

.section-description {
    margin: 0 0 1.5rem 0;
    text-align: center;
    color: var(--text-color);
    font-size: 0.95rem;
    line-height: 1.4;
    opacity: 0.9;
    padding-left: 0;
    background: linear-gradient(90deg, var(--primary-color), var(--secondary-color));
    color: white !important;
    padding: 1rem 2.5rem;
    border-radius: 50px;
    
    font-weight: 600;
    transition: all 0.3s ease;
    
    font-size: 1rem;
    text-align: center;
    
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.file-input-hidden {
    position: absolute;
    opacity: 0;
    width: 1px;
    height: 1px;
    pointer-events: none;
}

/* Enhanced visual cues for clickable upload sections */
.upload-section {
    position: relative;
    
    transition: all 0.3s ease;
    border: 2px dashed transparent;
}

.upload-section:hover {
    border-color: rgba(255, 255, 255, 0.3);
    background-color: rgba(0, 0, 0, 0.15);
    transform: translateY(-2px);
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
}

.upload-section:active {
    transform: translateY(0);
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.15);
}

/* Add subtle animation to indicate interactivity */
.upload-section::before {
    content: "👆";
    position: absolute;
    top: 1rem;
    right: 1rem;
    font-size: 1.2rem;
    opacity: 0.6;
    transition: opacity 0.3s ease;
}

.upload-section:hover::before {
    opacity: 1;
    animation: pulse 2s infinite;
}

@keyframes pulse {
    0%, 100% { transform: scale(1); }
    50% { transform: scale(1.1); }
}

